body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,body,#root {
	height: 100%;
	width: 100%;
}

* {
	font-size: 100%;
	font-family: "Skia";
}

:root {
  --main-color: #E8E8E8;
  --font-color: #96939B;
  --btn-font-color: white;
  --primary-btn-color: #75BCC6;
  --error-color: #ed6173;
  --accent-color: #75BCC6;
}

.App {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	height: 100%;
	width: 100%;
}

.MeasurementsPage, .TutorialPage {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100%;
	width: 100%;
}

.Measurements {
	display: flex;
	flex-direction: column;
	height: 100%;
	width: 100%;
	justify-content: center;
	align-items: center;
}

.LogoPanel {
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
	height: 20%;
	width: 100%;
}

.TitleImage {
	height: 40%;
}

.ContentPanelMeasurements, .GeneratePage {
	display: flex;
	flex-direction: row;
	justify-content: space-evenly;
	align-items: center;
	height: 90%;
	width: 100%;
	font-size: 4em;
}

.GeneratePage {
	flex-direction: column;
}

.MeasurementButton {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	cursor: pointer;
	font-size: .6em;
	background-color: #E8E8E8;
	background-color: var(--main-color);
	color: white;
	color: var(--btn-font-color);
	text-align: center;
	border-radius:30px;
	height: 30%;
	width: 20%;
	padding: 5%;
}

.BuiltInButtonPanel {
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	align-items: center;
	width: 100%;
	height: 50%;
}

.HeaderPanel {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 20%;
	width: 100%;
	font-size: 1.2em;
}

.ButtonPanel {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	height: 80%;
	width: 100%;
}

.SizeButtonPanel {
	display: flex;
	flex-direction: column;
	align-items: center;
	height: 50%;
	width: 100%;
	font-size: 1em;
}

.PatternDisplay {
	display: flex;
	align-items: center;
	justify-content: center;
	height: 80%;
	width: 70%;
	background-color: #E8E8E8;
	background-color: var(--main-color);
}


.PrintButtonPanel {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	height: 10%;
	width: 100%;
}

.TutorialPanel {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	background-color: #E8E8E8;
	background-color: var(--main-color);
	color: #96939B;
	color: var(--font-color);
	font-size: .6em;
	text-align: center;
	height: 70%;
	width: 60%;
	padding: 5%;
}

.MeasurementPanel {
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
	height: 100%;
	width: 50%;
}

.CuteButton {
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: #75BCC6;
	background-color: var(--primary-btn-color);
	cursor: pointer;
	font-size: .5em;
	color: white;
	color: var(--btn-font-color);
	border-radius: 5px;
}


.PrintButton {
	height: 100%;
	width: 20%;
}


.NextButton {
	height: 100%;
	width: 30%;
}


.MeasurementImage {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100%;
	width: 50%;
}

.MeasurementTitle {
	display: flex;
	height: 10%;
	width: 100%;
	font-size: 4em;
}
.MeasurementDescription {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 50%;
	width: 100%;
	font-size: 2em;
}

.MeasurementLabelPanel {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 10%;
	width: 100%;
	font-size: 1em;
}

.MeasurementErrorPanel {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 10%;
	width: 100%;
	font-size: 1.5em;
	color: #ed6173;
	color: var(--error-color);
}

.MeasurementLabel {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100%;
	width: 50%;
	font-size: 2em;
}

